.collection{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: 1rem 5rem;
    margin: 0 auto;
    margin-top: 3rem;
    @include t-bold();
    word-break: break-word;
    &-info{
        padding-top: 3rem;
        padding-bottom: 2rem;
        
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 30rem;
        width: 45%;
        margin-left: 5rem;
    }
    &-company{
        color: $orangeText;
        font-size: 1rem;
        

    }
    &-price{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        position: relative;
        width: 11.5rem;
    }
    &-descp{
        @include t-main();
    }
    &-discount{
        display: block;
        margin-top: 0.5rem;
        align-items: center;
        background-color: $paleBack;
        width: 3rem;
        height: 1.3rem;
        text-align: center;
        color: $textActive;
        font-size: 1rem;
    }
    &-trough{
        position: absolute;
        bottom: -2rem;
        text-decoration: line-through;
        @include t-main();
    }
}