.box{
    display: flex;
    flex-direction: column;
    &-current{
        width: 30rem;
        height: 30rem;
        position: relative;
        overflow: hidden;
        border: 3px solid $mainBorder;
        border-radius: 1rem;
        &_img{
            position: absolute;
            width: 30rem;
            height: auto;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            opacity: 0;
            transition: all 1s;
        }
        &_animation{
            opacity: 1;
        }
    }
    &-list{
        margin-top: 1rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    &-item{
        width: 6rem;
        height: 6rem;
        border: 3px solid $LightBoxBorder;
        border-radius: 1rem;
        position: relative;
        cursor: pointer;
        z-index: 1;
        &:hover{
            &::before{
                display: block;
            }
        }
        &::before{
            display: none;
            content: "";
            width: 100%;
            height: 100%;
            border-radius: 1rem;
            z-index: 3;
            position: absolute;
            background-color: $opacityLightBox;
        }
        &_img{
            z-index: 1;
            width: 3rem;
            height: auto;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            
        }
    }
    &-light{
        border-color: $mainBorder;
        &::before{
            display: block;
            content: "";
            width: 100%;
            height: 100%;
            border-radius: 1rem;
            z-index: 3;
            position: absolute;
            background-color: $opacityLightBox;
        }
    }

}
